export default {
	data() {
		return {
			isCollect: false
		};
	},
	methods: {
		handleCollect() {
			this.isCollect = !this.isCollect
			if(this.isCollect) {
				uni.showToast({
					title: '收藏成功'
				})
			}else {
				uni.showToast({
					title: '已取消'
				})
			}
			
		}
	}
}
